<template>
	<div id="scan" class="content">
		<TopBar title='扫一扫' @click-left="back()" right-text="相册" @click-right="scanPicture">
			<!--<template slot="right"></template> 支持title,right-->
		</TopBar>
		<div id="bcid" style="margin-top:-5px;">
			<!--盛放扫描控件的div-->
		</div>
	</div>
</template>

<script type='text/ecmascript-6'>
	let scan = null;
	import TopBar from '@/components/TopBar'
	import Vue from 'vue'
	import {
		Toast
	} from 'vant'
	
	Vue.use(Toast)
	export default {
		name: "ScanQRCode",
		components: {
			TopBar: TopBar,

		},

		created() {

		},
		activated() {
			//this.startRecognize();
		},
		mounted() {
			setTimeout(() => {
				this.startRecognize();
			}, 500)

		},
		methods: {
			back() {
				this.$router.push({
					path: "/Home"
				})
			},
			/* eslint-disable */
			startRecognize() {
				let _this = this;

				if (!window.plus) return;
				//创建扫描控件
				scan = new plus.barcode.Barcode('bcid',
					[plus.barcode.QR, plus.barcode.EAN8, plus.barcode.EAN13], {
						frameColor: "#00FF00",
						scanbarColor: "#00FF00"
					});
				//开始扫描
				scan.start();
				scan.onmarked = this.onmarked;
			},
			onmarked(type, result, file) {
				let codes = result.split(":");
				let key = codes[0];
				let value = codes[1];
				switch (type) {
					case plus.barcode.QR:
						type = 'QR';
						break;
					case plus.barcode.EAN13:
						type = 'EAN13';
						break;
					case plus.barcode.EAN8:
						type = 'EAN8';
						break;
					default:
						Toast('无法识别的二维码!')
						scan.start();
						break;
				}
				result = result.replace(/\n/g, '');
				//console.log(_this)
				scan.close();
				this.$route.params.fun(this, result);

			},
			scanPicture() {
				let instance = this
				plus.gallery.pick(function(path) {
					plus.barcode.scan(path, instance.onmarked, function(error) {
						Toast('无法识别此图片!')
					});

				});
			}
		},
		beforeDestroy() {
			if (scan) {
				//关闭扫描控件
				scan.close();
			}
		},
		computed: {

		},
		/* eslint-disable */
	}
</script>

<style lang="css">
	.qr-code {
		background-color: #FAF8F5;
	}

	#bcid {
		width: 100%;
		position: absolute;
		left: 0;
		right: 0;
		top: 50px;
		bottom: 3rem;
		text-align: center;
		color: #fff;
		background: #000;
	}
</style>
