<template>
	<div id="deptList" class="content">
		<TopBar title='设备数据列表' @click-left="back">
		</TopBar>
		<form action="/">
		<van-search
			v-model="data.keyword"
			show-action
			shape="round"
			placeholder="搜索企业/设备"
			@search="onSearch"
		>
			<template #action>
				<div @click="onSearch">搜索</div>
			</template>
		</van-search>
		</form>
		<van-tabs v-model="active" line-width="0px" @click="changeTab">
			<van-tab :name="item.name" v-for="(item,i) in statusList" :key="i" >
				<template #title> <van-tag round type="primary" color="#E9EAEC" :text-color="active==item.name?'#ee0a24':'black'" size="medium">{{item.title}}</van-tag></template>
				<van-collapse v-model="activeName" accordion>
					<van-pull-refresh v-model="refreshing" @refresh="onRefresh">
						<van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
							<van-collapse-item v-for="(device,j) in list" :key="j" is-link >
								<template #title>
									<div><span>{{device.companyName}}</span>/<span>{{device.workshopName}}</span>/<span>{{device.workstationName}}</span>/<span>{{device.equipmentName}}</span></div>
									<div style="float:left;">
										<div class="num">{{getMaxSpeed(device.sz,device.sy,device.sx)}}</div>
										<div class="maxValue">速度最大值</div>
									</div>
									<div style="float:right;margin-right:80px;">
										<div class="num">{{device.temperature?device.temperature:'--'}}℃</div>
										<div class="maxValue">温度最大值</div>
									</div>
								</template>
								<van-cell-group>
									<template #title>
										<van-icon name="location-o" /><span>{{device.componentName}}</span>
										<span style="float:right;">{{device.time}}</span>
									</template>
									<van-cell is-link :to="{path:'/monitoring/trendAnalysis',name:'TrendAnalysis',params:{componentType:'z',equipmentId:device.id,componentId:device.componentId,backUrl:'/monitoring/deviceList',name:active,equipmentStatus:data.equipmentStatus,componentName: device.componentName,suffix:suffixSZ}}">
										<template #title>
											<span>{{device.componentName}}</span>_<span>SZ</span>
										</template>
										<template #default>
											<span>{{device.sz}}</span>mm/s
										</template>
									</van-cell>
									<van-cell is-link :to="{path:'/monitoring/trendAnalysis',name:'TrendAnalysis',params:{componentType:'y',equipmentId:device.id,componentId:device.componentId,backUrl:'/monitoring/deviceList',name:active,equipmentStatus:data.equipmentStatus,componentName:device.componentName,suffix:suffixSY}}">
										<template #title>
											<span>{{device.componentName}}</span>_<span>SY</span>
										</template>
										<template #default>
											<span>{{device.sy}}</span>mm/s
										</template>
									</van-cell>
									<van-cell is-link :to="{path:'/monitoring/trendAnalysis',name:'TrendAnalysis',params:{componentType:'x',equipmentId:device.id,componentId:device.componentId,backUrl:'/monitoring/deviceList',name:active,equipmentStatus:data.equipmentStatus,componentName:device.componentName,suffix:suffixSX}}">
										<template #title>
											<span>{{device.componentName}}</span>_<span>SX</span>
										</template>
										<template #default>
											<span>{{device.sx}}</span>mm/s
										</template>
									</van-cell>
									<van-cell is-link :to="{path:'/monitoring/trendAnalysis',name:'TrendAnalysis',params:{componentType:'t',equipmentId:device.id,componentId:device.componentId,backUrl:'/monitoring/deviceList',name:active,equipmentStatus:data.equipmentStatus,componentName:device.componentName,suffix:suffixT}}">
										<template #title>
											<span>{{device.componentName}}</span>_<span>T</span>
										</template>
										<template #default>
											<span>{{device.t}}</span>℃
										</template>
									</van-cell>
								</van-cell-group>
								
							</van-collapse-item>
						</van-list>
					</van-pull-refresh>
				</van-collapse>
			</van-tab>
		</van-tabs>
		
	</div>
</template>

<script>
	import TopBar from '@/components/TopBar'

	import Vue from 'vue';
	import {
		List,
		Search,
		Tag,
		Collapse,
		CollapseItem,
		Toast    
	} from 'vant';

	Vue.use(List);
	Vue.use(Search);
	Vue.use(Tag);
	Vue.use(Collapse);
	Vue.use(CollapseItem);
	Vue.use(Toast);
	export default {
		name: "DeviceList",
		components: {
			TopBar: TopBar,
		},
		data() {
			return {
				list: [],
				loading: false,
				finished: false,
				refreshing: false,
				page: 1,
				limit: 50,
				backUrl: "/home",
				active: 'A',
				activeName: '',
				data: {
					keyword:"",
					companyId: "",
					equipmentStatus: "",
					workshopId: localStorage.getItem("workshopId"),
					factoryId: localStorage.getItem("factoryId")
				},
				statusList:[
					{
						name:'all',
						title:'不限'
					},
					{
						name:'A',
						title:'A区',
						equipmentStatus: 1
					},
					{
						name:'B',
						title:'B区',
						equipmentStatus: 6
					},
					{
						name:'C',
						title:'C区',
						equipmentStatus: 5
					},
					{
						name:'D',
						title:'D区',
						equipmentStatus: 4
					},
					{
						name:'离线',
						title:'离线',
						equipmentStatus: 7
					},
					{
						name:'关机',
						title:'关机',
						equipmentStatus: 2
					},
				],
				suffixSZ:"_SZ",
				suffixSY:"_SY",
				suffixSX:"_SX",
				suffixT:"_T",
			};
		},
		created() {
			//console.log(this)
			this.active = this.$route.params.name;
			this.data.equipmentStatus = this.$route.params.equipmentStatus
			this.data.companyId = this.$store.getters.getUser.firstCompanyId
			console.log(this.data)
			// this.getDeviceList();
				

		},
		methods: {
			back() {

				this.$router.push({
					path: this.backUrl
				})
			},
			onLoad() {
				if (this.refreshing) {
					this.list = [];
					this.page = 1;
					this.refreshing = false;
				}
				let instance = this;
				this.combest.list(this, "/iotapp/equipment/queryEquipmentDataList", this.page, this.limit, this.data, function(response) {
						//instance.list.push(response.data.data);
						console.log("返回值")
						console.log(response);
						console.log("返回值")
						let data = response.data.data;
						instance.list.push.apply(instance.list, data);

						instance.loading = false;


						if (data.length < 1) {
							instance.page--;
							instance.finished = true;
						} else {
							instance.page++;
						}
					},
					function(error) {
						console.log(error)
					})

			},
			onRefresh() {
				// 清空列表数据
				this.finished = false;

				// 重新加载数据
				// 将 loading 设置为 true，表示处于加载状态
				this.loading = true;
				this.onLoad();
			},
			onSearch(){
				this.refreshing = true;
				this.onRefresh();
			},
			changeTab(name){
				const getStatus = name => (this.statusList.find(item => (item.name === name)).equipmentStatus);
				this.data.equipmentStatus = getStatus(name);
				
				this.refreshing = true;
				this.onRefresh();
			},
			getMaxSpeed(sz,sy,sx){
				let max = sz;
				if(sy > sz){
					max = sy;
				}
				if(sx > max){
					max = sx;
				}
				return max+"mm/s";
			},
			
			
			
		},
	};
</script>

<style>
	.num{
		font-size:12px;
		color:#ee0a24;
	}
	.maxValue{
		font-size:10px;
		color:#BEBEBE;
	}
</style>
