<template>
	<div id="my" class="content">
		<van-nav-bar title="信息" fixed>
			<template #right>
				<van-icon name="scan" size="18" class="leftBar" />
			</template>
		</van-nav-bar>
		<van-cell title="id" :value="id" />
		<van-cell title="标题" :value="subject" />


		<TagBar></TagBar>
	</div>
</template>

<script>
	import TagBar from '@/components/TagBar'
	import Vue from 'vue'
	import {
		Cell,
		CellGroup
	} from 'vant'

	Vue.use(Cell)
	Vue.use(CellGroup)
	export default {
		name: 'Test',
		components: {
			TagBar: TagBar
		},
		data() {
			//console.log(this.id)
			return {
				id: 0,
				subject:""
			}
		},
		created() {
			//console.log(this)
			//this.id = this.$route.params.id
			let instance=this;
			this.combest.get(this, "/approval/get",  {id:this.$route.params.id}, function(response) {
				console.log(response)
					console.log(response.data.data.subject)
					instance.id=response.data.data.id;
					instance.subject=response.data.data.subject
				},
				function(error) {
					console.log(error)

				})

		},
		methods: {

		}
	}
</script>

