import Vue from 'vue'
import App from './App'
import router from './router'
import Vant from 'vant'
import 'vant/lib/index.less'
import 'vant/lib/index.css'
import { Toast } from 'vant'
import CryptoJS from 'crypto-js'
import store from './store'
import mui from './lib/mui/js/mui.js'
import echarts from 'echarts'
//import './lib/mui/css/mui.css'
Vue.use(Vant)
Vue.use(Toast)
import axios from 'axios'
import {Combest} from'./js/combest.js'
Toast.setDefaultOptions({ duration: 1000 });
Vue.prototype.$axios = axios
Vue.prototype.$echarts = echarts
Vue.prototype.$CryptoJS = CryptoJS
Vue.prototype.combest=Combest
Vue.prototype.$mui=mui
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  render: h => h(App),
  router:router,
  store,
  created(){
	this.$axios.defaults.baseURL = this.$store.getters.getProtocol+"://"+this.$store.getters.getServer+":"+this.$store.getters.getPort;
	this.$axios.defaults.headers.common['combest-app'] = '';
	this.$axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
  }
}).$mount('#app')
