<template>
	<div id="recommend" class="content">
		<TopBar title='推荐我们' @click-left="back">
		</TopBar>
		<van-field name="recommendMessage" label="" readonly :value="message" placeholder="" input-align="center" />
		<div style="margin-left: 110px;margin-top: 50px;" class="qrcode" ref="qrCodeUrl"></div>
		<TagBar></TagBar>
	</div>
</template>

<script>
	import TagBar from '@/components/TagBar'
	import TopBar from '@/components/TopBar'
	import QRCode from 'qrcodejs2'
	import Vue from 'vue'
	import {
		Cell,
		CellGroup
	} from 'vant'

	Vue.use(Cell)
	Vue.use(CellGroup)
	export default {
		name: 'Recommend',
		components: {
			TagBar: TagBar,
			TopBar: TopBar,
		},
		data() {
			return {
				message: "扫码下载app",
				// 外网
				// qrText: "https://service.dcloud.net.cn/build/download/bb604550-5001-11eb-ae88-c7eb7eab2413"
				// 内网
				qrText:"http://192.168.10.111:8001/img/apk/H515386E6_0111085717.apk"
			}
		},
		methods: {
			back() {
				this.$router.push({
					path: "/me/my"
				})
			},
			creatQrCode() {
				console.log("生成二维码")
				let instance = this;
				let qrcode = new QRCode(this.$refs.qrCodeUrl, {
					text: instance.qrText,
					width: 200,
					height: 200,
					colorDark: '#000000',
					colorLight: '#ffffff',
				})
				console.log(qrcode);
			}
		},
		mounted() {
			this.creatQrCode();
		},
	}
</script>

