<template>
	<div id="deptList" class="content">
		<TopBar title='选择分厂' @click-left="back">
			<!-- <template slot="right">
				<van-popover v-model="showOptions" trigger="click" :actions="actions" @select="onSelect" placement="bottom-end">
				<template #reference>
					<span style="color:white;">更多操作</span>
				</template>
				</van-popover>
			</template> -->
		</TopBar>
		
		<van-pull-refresh v-model="refreshing" @refresh="onRefresh" class="searchContent">
			<van-cell  title="全部工厂" size="mini" style="border-bottom: 1px solid #ccc!important;padding: 14px;border-color: #ccc!important;margin-top: 1px;" is-link  :to="{path:'/home',name:'Home',params:{}}">
				<!-- <template #default>
					<div>{{item.companyName}}</div>
				</template> -->
			</van-cell>
			<van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
				<van-cell v-for="(item,i) in list" :key="i" :title="item.companyName" size="mini" style="border-bottom: 1px solid #ccc!important;padding: 14px;border-color: #ccc!important;margin-top: 1px;" is-link :to="{path:'/me/choiseWorkShop',name:'ChoiseWorkShop',params:{id:item.id ,backUrl:backUrl}}" >
					<!-- <template #default>
						<div>{{item.companyName}}</div>
					</template> -->
				</van-cell>
			</van-list>
		</van-pull-refresh>
	</div>
</template>

<script>
	import TopBar from '@/components/TopBar'

	import Vue from 'vue';
	import {
		List,
	} from 'vant';

	Vue.use(List);
	export default {
		name: "ChoiseSecondCompany",
		components: {
			TopBar: TopBar,
		},
		data() {
			return {
				list: [],
				loading: false,
				finished: false,
				refreshing: false,
				page: 1,
				limit: 50,
				backUrl: "/me/my"
			};
		},

		methods: {
			back() {

				this.$router.push({
					path: "/me/choiseCompany",
					name:"ChoiseCompany",
					params:{backUrl:this.backUrl}
				})
			},
			onLoad() {
				if (this.refreshing) {
					this.list = [];
					this.page = 1;
					this.refreshing = false;
				}
				let instance = this;
				if(this.$route.params.backUrl){
					this.backUrl = this.$route.params.backUrl;
				}
				this.combest.list(this, "/company/querySecondCompanyList", this.page, this.limit, this.data, function(response) {
						//instance.list.push(response.data.data);
						
						instance.list.push.apply(instance.list, response.data.data);

						instance.loading = false;

						if (response.data.data.length < 1) {
							//instance.page--;
							instance.finished = true;
						} else {
							instance.page++;
							instance.finished = true				//因为pageHelper的原型，暂时这么处理，因为pagHelper无论发送什么请求都会返回数据
						}
					},
					function(error) {
						console.log(error)
					})

			},
			
			onRefresh() {
				// 清空列表数据
				this.finished = false;

				// 重新加载数据
				// 将 loading 设置为 true，表示处于加载状态
				this.loading = true;
				this.onLoad();
			},
		
		},
	};
</script>

<style>
</style>
