<template>
	<div id="main">
		<van-nav-bar title="监测" fixed @click-right="scan">

			<template #right>
				<van-icon name="scan" size="18" class="leftBar" />
			</template>

		</van-nav-bar>
		<router-view></router-view>
		<van-tabs v-model="active" v-if="!showCompanyList" @click="changeTab" style="margin-bottom:50px;">
			<van-tab title="实时数据" name="实时数据">
				<van-cell title="全部设备" is-link :to="{path:'/main/choiseCompany',name:'MainChoiseCompany',params:{showCompanyList:true,backUrl: '/home'}}" />
				<div ref="container" style="width:100%;height: 200px"></div>
				
			</van-tab>
			<van-tab title="趋势变化" name="趋势变化">
				<van-cell title="全部设备" is-link :to="{path:'/main/choiseCompany',name:'MainChoiseCompany',params:{showCompanyList:true,backUrl: '/home'}}" />
				<div ref="trendContainer" style="width:100%;height: 200px"></div>
				<van-tabs type="card" color="#177CB0" @click="getTrendData">
					<van-tab title="前一天" name="1"></van-tab>
					<van-tab title="前一周" name="2"></van-tab>
					<van-tab title="前一月" name="3"></van-tab>
					<van-tab title="自定义" name="">
						<van-cell>
							<template #icon>
								<van-icon name="calendar-o" class="timeIcon"/>
							</template>
							<template #title>
								<van-field v-model="startTime" placeholder="请选择开始时间" readonly @click="showStart = true"/>
								<van-popup v-model="showStart" position="bottom">
									<van-datetime-picker
										type="date"
										v-model="currentDate"
										@confirm="onStart"
										@cancel="showStart = false;startTime = ''"
										cancel-button-text="清除"
										ref = "start"
									/>
								</van-popup>
							</template>
							<template #default>
								<van-field v-model="endTime" placeholder="请选择结束时间" readonly @click="showEnd = true"/>
								<van-popup v-model="showEnd" position="bottom">
									<van-datetime-picker
										type="date"
										v-model="currentDate"
										@confirm="onEnd"
										@cancel="showEnd = false;endTime = ''"
										cancel-button-text="清除"
										ref = "end"
									/>
								</van-popup>
							</template>
						</van-cell>
					</van-tab>
				</van-tabs>
			</van-tab>
			<van-cell-group>
				<van-cell :to="{path:'/monitoring/deviceList',name:'DeviceList',params:{equipmentStatus:4,name:'D'}}">
					<template #icon>
						<van-icon name="stop-circle" color="red" class="signIcon"/>
					</template>
					<template #title>
						<span>D区</span>
						<span style="float:right;">{{realTimeData['D区']}}</span>
					</template>
					<template #default>
						<span>{{getPercent(realTimeData['D区'],realTimeData.totalNum)}}</span>
					</template>
				</van-cell>
				<van-cell :to="{path:'/monitoring/deviceList',name:'DeviceList',params:{equipmentStatus:5,name:'C'}}">
					<template #icon>
						<van-icon name="stop-circle" color="orange" class="signIcon"/>
					</template>
					<template #title>
						<span>C区</span>
						<span style="float:right;">{{realTimeData['C区']}}</span>
					</template>
					<template #default>
						<span>{{getPercent(realTimeData['C区'],realTimeData.totalNum)}}</span>
					</template>
				</van-cell>
				<van-cell :to="{path:'/monitoring/deviceList',name:'DeviceList',params:{equipmentStatus:6,name:'B'}}">
					<template #icon>
						<van-icon name="stop-circle" color="#CDCD00" class="signIcon"/>
					</template>
					<template #title>
						<span>B区</span>
						<span style="float:right;">{{realTimeData['B区']}}</span>
					</template>
					<template #default>
						<span>{{getPercent(realTimeData['B区'],realTimeData.totalNum)}}</span>
					</template>
				</van-cell>
				<van-cell :to="{path:'/monitoring/deviceList',name:'DeviceList',params:{equipmentStatus:1,name:'A'}}">
					<template #icon>
						<van-icon name="stop-circle" color="green" class="signIcon"/>
					</template>
					<template #title>
						<span>A区</span>
						<span style="float:right;">{{realTimeData['A区']}}</span>
					</template>
					<template #default>
						<span>{{getPercent(realTimeData['A区'],realTimeData.totalNum)}}</span>
					</template>
				</van-cell>
				
				<van-cell :to="{path:'/monitoring/deviceList',name:'DeviceList',params:{equipmentStatus:7,name:'离线'}}">
					<template #icon>
						<van-icon name="stop-circle" color="brown" class="signIcon"/>
					</template>
					<template #title>
						<span>离线</span>
						<span style="float:right;">{{realTimeData['离线']}}</span>
					</template>
					<template #default>
						<span>{{getPercent(realTimeData['离线'],realTimeData.totalNum)}}</span>
					</template>
				</van-cell>
				<van-cell :to="{path:'/monitoring/deviceList',name:'DeviceList',params:{equipmentStatus:2,name:'关机'}}">
					<template #icon>
						<van-icon name="stop-circle" color="gray" class="signIcon"/>
					</template>
					<template #title>
						<span>关机</span>
						<span style="float:right;">{{realTimeData['关机']}}</span>
					</template>
					<template #default>
						<span>{{getPercent(realTimeData['关机'],realTimeData.totalNum)}}</span>
					</template>
				</van-cell>
			</van-cell-group>
		</van-tabs>
		
	</div>
</template>

<script>
	import Vue from 'vue'
	import {
		Tab,
		Tabs,
		Toast 
	} from 'vant'

	Vue.use(Tab);
	Vue.use(Tabs);
	export default {
		name: 'Main',
		components: {
		},
		props: {
			factoryId: Number,
			workshopId:Number,
		},
		data() {
			return {
				active: '实时数据',
				myChart: "",
				trendChart: "",
				realTimeData: {},
				timeFlag: "1",
				startTime: "",
				endTime: "",
				showStart: false,
				showEnd: false,
				showCompanyList: false,
				currentDate: new Date(),
				equipmentName: ""
			};
		},
		created() {
			if(this.$route.params.showCompanyList){
				this.showCompanyList = this.$route.params.showCompanyList
			}
			console.log(this.workshopId)
			localStorage.setItem('workshopId', this.workshopId?this.workshopId:"");
			localStorage.setItem('factoryId', this.factoryId?this.factoryId:"");
			this.getRealTimeData();
			
		},
		methods: {
			scan() {
				let instance = this;
				this.$router.push({path:"/moudules/scanQRCode",name:"ScanQRCode",params:{
					fun:function(vue,result){
						// alert(result)
						// alert(vue.$route.params.subject)
						console.log("发起了扫描二维码");
						console.log("result");
						console.log(result);
						console.log(JSON.stringify(vue.$route.params));
						console.log(vue.$route.params.subject);
						instance.combest.get(instance, "/iotapp/workOrder/isModuleWorkOrder", {
								moduleCode: instance.$route.params.result
							}, function(response) {
								console.log("判断该模组所属工单是否属于当前用户")
								console.log(JSON.stringify(response.data.data))
								if(response.data.data == true){
									vue.$router.push({path: "/scan/WorkOrderInformation", name:"WorkOrderInformation",params:{result:result,from:'main'}})
								}else{
									Toast.setDefaultOptions({ duration: 1500 });
									Toast.fail('该工单不属于该用户，没有权限查看');
									vue.$router.push({path: "/main", name:"Main"})
								}
							},
							function(error) {
								console.log(error)
						})
						
					},
					backUrl:"/main",
					subject:"测试"
				}})
			},
			getEchartData() {
				this.$nextTick(() => {
					const container = this.$refs.container
					if (container) {
						this.myChart = this.$echarts.init(container);
					}
				});
				
			},
			onStart(){
				let picker = this.$refs.start.getPicker();
				this.startTime = picker.getColumnValue(0) + '-' + picker.getColumnValue(1) + '-' + picker.getColumnValue(2);
				this.showStart = false;
				this.getTrendData(0);
			},
			onEnd() {
				let picker = this.$refs.end.getPicker();
				this.endTime = picker.getColumnValue(0) + '-' + picker.getColumnValue(1) + '-' + picker.getColumnValue(2);
				this.showEnd = false;
				this.getTrendData(0);
			},
			selectDevice(){

			},
			changeTab(name){
				if(name == '实时数据'){
					this.getRealTimeData();
				}else{
					this.$nextTick(() => {
						const trendContainer = this.$refs.trendContainer
						if (trendContainer){
							this.trendChart = this.$echarts.init(trendContainer);
						}
						//获取趋势分析
						this.getTrendData();
					});
					
				}
			},
			buildData(){
				console.log(this)
				console.log(this.myChart)
				if(!this.myChart){
					return false;
				}
				this.myChart.setOption(
					({
						tooltip: {
							trigger: 'item',
							formatter: '{b} : {c} ({d}%)'
						},
						color: ['#008000','#CDCD00','#FFA500','#FF0000','#A52A2A','#808080'],
						title: {
							zlevel: 0,
							text: [
								this.realTimeData.totalNum+'\n{value|机泵台数}',
							].join('\n'),
							top: 'center',
							left: '48%',
							textAlign: 'center',
							textStyle: {
								color: '#177CB0',
								rich: {
									value: {
										color: 'Grey',
									},
								},
							},
						},
						series: {
							name: this.equipmentName,
							type: 'pie',
							// roseType: 'area',
							radius: [50, 70],
							data: [
								{value: this.realTimeData['A区'], name: 'A区'},
								{value: this.realTimeData['B区'], name: 'B区'},
								{value: this.realTimeData['C区'], name: 'C区'},
								{value: this.realTimeData['D区'], name: 'D区'},
								{value: this.realTimeData['离线'], name: '离线'},
								{value: this.realTimeData['关机'], name: '关机'}
							],
						},
					}),
				);
				window.addEventListener("resize", function() {
					if(this.myChart){
						this.myChart.resize() 
					}
				});
					
			},
			buildTrendData(){
				this.trendChart.setOption(
					({
						tooltip: {
							trigger: 'item',
							formatter: '{b} : {c} ({d}%)'
						},
						color: ['#008000','#CDCD00','#FFA500','#FF0000','#A52A2A','#808080'],
						title: {
							zlevel: 0,
							text: [
								this.realTimeData.totalNum+'\n{value|机泵台数}',
							].join('\n'),
							top: 'center',
							left: '48%',
							textAlign: 'center',
							textStyle: {
								color: '#177CB0',
								rich: {
									value: {
										color: 'Grey',
									},
								},
							},
						},
						series: {
							name: this.equipmentName,
							type: 'pie',
							// roseType: 'area',
							radius: [50, 70],
							data: [
								{value: this.realTimeData['A区'], name: 'A区'},
								{value: this.realTimeData['B区'], name: 'B区'},
								{value: this.realTimeData['C区'], name: 'C区'},
								{value: this.realTimeData['D区'], name: 'D区'},
								{value: this.realTimeData['离线'], name: '离线'},
								{value: this.realTimeData['关机'], name: '关机'}
							],
						},
					}),
				);
				window.addEventListener("resize", function() {
					if(this.trendChart){
						this.trendChart.resize() 
					}
				});
					
			},
			getTrendData(name){
				console.log(name)
				if(name){
					this.timeFlag = name;
					console.log(this.timeFlag)
				}
				let instance = this;
				let factoryId = localStorage.getItem('factoryId');
				let workshopId = localStorage.getItem('workshopId');
				this.combest.get(this, "/iotapp/equipment/queryEquipmentHistoryData",  {
					companyId:this.$store.getters.getUser.firstCompanyId,
					timeFlag: this.timeFlag,
					startTime: this.startTime,
					endTime: this.endTime,
					factoryId: factoryId,
					workshopId: workshopId
				}, function(response) {
					console.log(response)
					console.log(response.data)
					instance.realTimeData = response.data.data;
					instance.buildTrendData();
				},
				function(error) {
					console.log(error)

				})

				
			},
			getRealTimeData(){
				let instance = this;
				let factoryId = localStorage.getItem('factoryId');
				let workshopId = localStorage.getItem('workshopId');
				this.combest.get(this, "/iotapp/equipment/queryEquipmentRealTimeData",  {
					companyId:this.$store.getters.getUser.firstCompanyId,
					factoryId: factoryId,
					workshopId: workshopId
					}, function(response) {
					console.log(response)
					console.log(response.data)
					instance.realTimeData = response.data.data;
					instance.buildData();
				},
				function(error) {
					console.log(error)

				})
			},
			getPercent(num, total) {
				num = parseFloat(num);
				total = parseFloat(total);
				if (isNaN(num) || isNaN(total)) {
					return "-";
				}
				return total <= 0 ? "0%" : (Math.round(num / total * 100) )+"%";
			} 
		},
		mounted(){
			this.getEchartData()  
		},
	}
</script>

<style>
	.signIcon{
		font-size:10px !important;
		margin: 6px;
	}
</style>
