<template>
	<div id="workOrderInformation" class="content">
		<TopBar title='工单信息' @click-left="back">
			<template slot="right">
				<div v-show="showInstallModule" style="color: yellow;"   @click="installModuleClick">
					安装
				</div>
			</template>
		</TopBar>
		
		
		<van-form @submit="onSubmit" style="margin-bottom: 80px;">
			<van-field required readonly v-model="installedStatus" label="工单状态"  right-icon="warning-o"	/>
			<van-field readonly v-model="moduleCode"   label="模组编码"  />
			
			<van-field readonly v-model="userName" label="巡检人"  />
			<van-field readonly v-model="workDate" label="巡检日期"  />
			<van-field readonly v-model="companyName" label="企业名称"  />
			<van-field readonly v-model="workshopName" label="部署车间"  />
			<van-field readonly v-model="equipmentStatusName" label="设备状态"  />
			<van-field readonly v-model="equipmentNo" label="设备编号"  />
			<van-field readonly v-model="equipmentName" label="设备名称"  />
			<van-field readonly v-model="categoryName" label="设备分类"  />
			<van-field readonly v-model="equipmentManagerName" label="设备管理员"  />
			<van-field readonly v-model="phoneNum" label="联系手机"  />
			<van-field readonly v-model="wechatAccount" label="联系微信"  />
			
			<!-- <div  style="margin: 16px; bottom:0;width:92%;position:fixed">
				<van-button  plain round block type="info"  native-type="submit">
					一键安装
				</van-button>
			</div> -->
		</van-form>
	</div>
</template>

<script>
	import TopBar from '@/components/TopBar'
	import Vue from 'vue'
	import {
		Cell,
		CellGroup,
		Toast,
		Notify
	} from 'vant'

	Vue.use(Cell)
	Vue.use(CellGroup)
	Vue.use(Notify);
	export default {
		name: 'WorkOrderInformation',
		components: {
			TopBar: TopBar
		},
		data() {
			return {
				id:"",
				moduleCode:"",
				workUserId:"",
				workDate:"",
				companyName:"",
				workshopName:"",
				equipmentStatus:"",
				equipmentStatusName:"",
				equipmentNo:"",
				equipmentName:"",
				categoryName:"",
				equipmentManagerName:"",
				phoneNum:"",
				wechatAccount:"",
				installed:"",
				installedStatus:"",
				back_url:"/main",
				showInstallModule:true
			}
		},
		created() {
			console.log("扫描二维码后跳转过来的值为")
			console.log(JSON.stringify(this.$route.params));
			console.log(this.$route.params);
			console.log(this.$route.params.result);
			let instance = this;
			
			instance.combest.get(instance, "/iotapp/workOrder/queryWorkOrderInfo", {
					moduleCode: instance.$route.params.result
				}, function(response) {
					console.log(JSON.stringify(response))
					console.log("获取返回值")
					console.log(response.data.data);
					console.log(JSON.stringify(response.data.data))
					console.log("获取返回值")
					Object.assign(instance, response.data.data)
					instance.getInstallStatus(response.data.data.installed)
					instance.getEquipmentStatusName(response.data.data.equipmentStatus)
				},
				function(error) {
					console.log(error)
				}
			)
			
			// this.combest.get(this, "/iotapp/workOrder/isModuleWorkOrder", {
			// 		moduleCode: this.$route.params.result
			// 	}, function(response) {
			// 		console.log(JSON.stringify(response.data.data))
			// 		if(response.data.data == true){
						
			// 		}				
			// 	},
			// 	function(error) {
			// 		console.log(error)
			// })
			
		
		},
		methods: {
			back() {
				let instance = this;
				//判断来源
				if(this.$route.params.from == "main"){
					this.$router.push({
						name:'Home'
					})
				}else if(this.$route.params.from == "my"){
					this.$router.push({
						name:'My'
					})
				}else if(this.$route.params.from == "sysMessageInfo"){
					this.$router.push({
						name:'SysMessageInfo',
						params:{
							id:instance.$route.params.sysMessageId
						}
					})
				}
				// console.log(instance.back_url);
				// this.$router.push({
				// 	path:instance.back_url
				// })
			},
			
			
			/**
			 * 点击提交按钮
			 */
			onSubmit() {
				
			},
			
			installModuleClick(){
				let instance = this;
				console.log("准备安装模组");
				// console.log(this.$data);
				instance.combest.get(instance, "/iotapp/workOrder/updateWorkOrder", {
					id: instance.id
				}, function(response) {
					console.log(JSON.stringify(response))
					if (response.data.code == 0) {
						Toast.success('安装模组成功!即将返回至列表页');
						Toast.setDefaultOptions({ duration: 1500 });
						instance.$router.push('/main');
					} else {
						Toast.setDefaultOptions({ duration: 1500 });
						Toast.success('安装模组失败!');
					}
				},
				function(error) {
					console.log(error)
				}, "application/json;charset=UTF-8")
			},
			
			/**
			 * @param {Object} value 根据传入的值动态显示是否已经安装
			 * 0：未安装
			 * 1：已安装
			 */
			getInstallStatus(value){
				console.log("显示工单状态");
				console.log(value)
				let instance = this;
				//只要不是从工单页面扫描进来的，不显示安装按钮
				if(instance.$route.params.from != "sysMessageInfo" && value == 0){
					instance.showInstallModule = false
					Notify({ type: 'warning', message: '若安装该模组请前往工单页面进行扫码安装' });
				}
				if(value == 0){
					instance.installedStatus = "未安装"
					
				}else if(value == 1){
					instance.installedStatus = "已安装"
					instance.showInstallModule = false
				}
			},
			
			/**
			 * @param {Object} value 根据传入的值动态显示设备的状态
			 */
			getEquipmentStatusName(value){
				console.log("显示设备状态")
				console.log(value);
				let instance = this;
				if(value == "1"){
					instance.equipmentStatusName = "运行"
				}
				if(value == "2"){
					instance.equipmentStatusName = "停机"
				}
				if(value == "3"){
					instance.equipmentStatusName = "维保"
				}
			}
		}
	}
</script>

<style>
</style>
