<template>
	<div id="main">
		<TopBar title='消息' @click-left="back">
		</TopBar>
		<div ref="getheight" style="height:45px;"></div>
		<van-tabs v-model="active" @click="changeTab" :sticky="true" :duration="0" :swipe-threshold="5"
		:offset-top="this.heightall" class="seckilling-tab">
			<van-tab title="事件" name="事件">
				<van-pull-refresh v-model="refreshingEvent" @refresh="onRefreshEventMsg">
					<van-list v-model="loadingEvent" :finished="finishedEvent" finished-text="没有更多了" @load="onLoadEventMsg"
					:immediate-check="false">
						<van-cell v-for="(item,i) in listEvent" :key="i" :title="item.title" size="mini" style="border-bottom: 1px solid #ccc!important;padding: 14px;border-color: #ccc!important;margin-top: 1px;margin-left: 5px;"
						is-link :to="{path:'/message/eventMessageInfo',name:'EventMessageInfo',params:{id:item.id}}">
							<template #icon>
								<van-icon  name="chat-o" class="timeIcon" style="margin-right: 10px;" />
							</template>
							<div style="margin-left: 10px;">{{item.companyName}}/{{item.equipmentName}}/{{item.componentName}}</div>
							<div style="margin-left: 10px;color: orange">{{getWarnLevelStatus(item.warnLevel)}}</div>
							<div style="margin-left: 10px;color: skyblue">{{item.createTime}}</div>
						</van-cell>
					</van-list>
				</van-pull-refresh>
			</van-tab>
			<van-tab title="系统" name="系统">
				<van-pull-refresh v-model="refreshingSys" @refresh="onRefreshSysMsg">
					<van-list v-model="loadingSys" :finished="finishedSys" finished-text="没有更多了" @load="onLoadSysMsg" :immediate-check="false">
						<van-cell v-for="(item,i) in listSys" :key="i" :title="item.title" size="mini" style="border-bottom: 1px solid #ccc!important;padding: 14px;border-color: #ccc!important;margin-top: 1px;margin-left: 5px;"
						is-link :to="{path:'/message/sysMessageInfo',name:'SysMessageInfo',params:{id:item.id}}">
							<template #icon>
								<van-icon v-if="item.readed == 0" name="chat-o" dot class="timeIcon" style="margin-right: 10px;" />
								<van-icon v-if="item.readed != 0" name="chat-o" class="timeIcon" style="margin-right: 10px;" />
							</template>
							<template #label>
								<div style="margin-left: 10px;">{{item.description}}</div>
								<div style="margin-left: 10px;color: skyblue">{{item.createTime}}</div>
							</template>
						</van-cell>
					</van-list>
				</van-pull-refresh>
			</van-tab>
		</van-tabs>
	</div>

	<!-- <TagBar></TagBar> -->


</template>

<script>
	import Vue from 'vue'
	// import TagBar from '@/components/TagBar'
	import TopBar from '@/components/TopBar'
	import {
		Grid,
		GridItem
	} from 'vant'

	Vue.use(Grid)
	Vue.use(GridItem)
	export default {
		name: 'Message',
		components: {
			// TagBar: TagBar,
			TopBar: TopBar
		},
		data() {
			return {
				active: '事件',
				listEvent: [],
				listSys: [],
				loadingEvent: false,
				loadingSys: false,
				finishedEvent: false,
				finishedSys: false,
				refreshingEvent: false,
				refreshingSys: false,
				pageSys: 1,
				pageEvent: 1,
				limit: 50,

				showDot: false,
				heightall: 0,
			};
		},
		created() {
			if (this.$route.params.active != null && this.$route.params.active == "系统") {
				this.active = "系统";
				this.onLoadSysMsg();
			} else {
				this.active = "事件";
				this.onLoadEventMsg();
			}

		},
		mounted() {
			this.$nextTick(function() {
				this.heightall = this.$refs.getheight.offsetHeight
				console.log(this.heightall)
			})
		},
		methods: {
			changeTab(name) {
				if (name == '事件') {
					console.log(this);
					this.finishedEvent = false;
					this.finishedSys = false;
					this.listEvent = [];
					this.pageEvent = 1;
					console.log("查看事件消息")
					this.onLoadEventMsg();
				} else {
					console.log(this);
					this.finishedSys = false;
					this.finishedEvent = false;
					this.listSys = [];
					this.pageSys = 1;
					console.log("查看系统消息")
					this.onLoadSysMsg();
				}
			},
			back() {
				this.$router.push({
					name: 'Home'
				})
			},
			onLoadEventMsg() {
				console.log("重新加载事件消息的次数");
				if (this.refreshingEvent) {
					this.listEvent = [];
					this.pageEvent = 1; //重置page
					this.refreshingEvent = false;
				}
				let instance = this;
				this.combest.list(this, "/warnLog/list", this.pageEvent, this.limit, this.data, function(
						response) {
						//instance.list.push(response.data.data);
						console.log("返回值")
						console.log(response);
						console.log("返回值")
						instance.listEvent.push.apply(instance.listEvent, response.data.data);

						instance.loadingEvent = false;
						console.log("返回值的长度")
						console.log("response.data.data.length>>")
						console.log(response.data.data.length);
						console.log("请求的参数：page>>" + instance.pageEvent + " limit>>" + instance.limit)
						if (response.data.data.length < 1) {
							instance.pageEvent--;
							instance.finishedEvent = true;
						} else {
							instance.pageEvent++;
						}
					},
					function(error) {
						console.log(error)
					})

			},
			onLoadSysMsg() {
				if (this.refreshingSys) {
					this.listSys = [];
					this.pageSys = 1; //重置page
					this.refreshingSys = false;
				}
				let instance = this;
				this.combest.list(this, "/iotapp/message/querySysMsgListPaging", this.pageSys, this.limit, this.data, function(
						response) {
						//instance.list.push(response.data.data);
						console.log("返回值")
						console.log(response);
						console.log("返回值")
						instance.listSys.push.apply(instance.listSys, response.data.data);

						instance.loadingSys = false;

						console.log("response.data.data.length>>")
						console.log(response.data.data.length);
						if (response.data.data.length < 1) {
							instance.pageSys--;
							instance.finishedSys = true;
						} else {
							instance.pageSys++;
						}
					},
					function(error) {
						console.log(error)
					})

			},
			onRefreshSysMsg() {
				console.log("调用了下拉刷新>系统消息")
				// 清空列表数据
				this.finishedSys = false;

				// 重新加载数据
				// 将 loading 设置为 true，表示处于加载状态
				this.loadingSys = true;
				this.onLoadSysMsg();
			},
			onRefreshEventMsg() {
				console.log("调用了下拉刷新>事件消息")
				// 清空列表数据
				this.finishedEvent = false;

				// 重新加载数据
				// 将 loading 设置为 true，表示处于加载状态
				this.loadingEvent = true;
				this.onLoadEventMsg();
			},
			getWarnLevelStatus(value){
				switch(value){
					case 7:
						return "离线";
					case 6:
						return "高风险";
					case 5:
						return "风险";
					case 4:
						return "异常";
					case 3:
						return "维保";
					case 2:
						return "停机";
					case 1:
						return "正常";
				}
			}
		}
	}
</script>

<style>

</style>
