<template>
	<div id="my" class="content">
		<TopBar title='趋势分析' @click-left="back">
			<template slot="right">
				<van-popover v-model="showOptions" trigger="click" :actions="actions" @select="onSelect" placement="bottom-end">
				<template #reference>
					<span style="color:white;"><van-icon name="ellipsis" size="20px"/></span>
				</template>
				</van-popover>
			</template>
		</TopBar>
		<van-cell-group>
			<van-cell>
				<template #icon>
					<van-icon name="calendar-o" class="timeIcon"/>
				</template>
				<template #title>
					<van-field v-model="startTime" placeholder="请选择开始时间" readonly @click="showStart = true"/>
					<van-popup v-model="showStart" position="bottom">
						<van-datetime-picker
							type="date"
							v-model="currentDate"
							@confirm="onStart"
							@cancel="showStart = false;startTime = ''"
							cancel-button-text="清除"
							ref = "start"
						/>
					</van-popup>
				</template>
				<template #default>
					<van-field v-model="endTime" placeholder="请选择结束时间" readonly @click="showEnd = true"/>
					<van-popup v-model="showEnd" position="bottom">
						<van-datetime-picker
							type="date"
							v-model="currentDate"
							@confirm="onEnd"
							@cancel="showEnd = false;endTime = ''"
							cancel-button-text="清除"
							ref = "end"
						/>
					</van-popup>
				</template>
			</van-cell>
			<Block title="趋势分析项目选择" :padding="true" v-show="componentType != 't'">
				<van-field name="projectType" readonly is-link :value="projectTypeValue" clickable placeholder="点击选择趋势分析项目" @click="showProjectType = true"/>
				<van-popup v-model="showProjectType" position="bottom">
					<van-picker
						show-toolbar
						:columns="projectTypeOption"
						@confirm="onProjectType"
						@cancel="showProjectType = false"
						ref="projectType"
					/>
				</van-popup>
			</Block>
			<Block title="风险预警选择" :padding="true">
				<van-checkbox-group v-model="risk" direction="horizontal" ref="riskGroup" style="height:30px;" @change="builCheck">
					<van-checkbox name="all" @click="toggleAll">全选</van-checkbox>
					<van-checkbox name="fault">高风险</van-checkbox>
					<van-checkbox name="risk">风险</van-checkbox>
				</van-checkbox-group>
			</Block>
			
			<van-field v-model="deviceValue" is-link readonly placeholder="请选择设备" @click="showDevice = true" v-if="showdeviceSelect"/>
			<van-popup v-model="showDevice" round position="bottom">
				<van-cascader
					v-model="cascaderValue"
					title="请选择所在地区"
					:options="options"
					@close="showDevice = false"
					@finish="onFinish"
				/>
			</van-popup>
		</van-cell-group>
		
		<div ref="container" style="width:100%;height: 500px"></div>
		<Block :padding="true">
			<van-cell title="参数信息" v-if="JSON.stringify(params)!='{}'">
				<template #label>
					<div>加速度：{{params.acc}}</div>
					<div>振幅：{{params.amp}}，测点温度：{{params.temperature}}℃</div>
					<div>风险数：{{params.fx}}，高风险数：{{params.gfx}}</div>
					<div>上报时间：{{params.createTime}}</div>
					<div>监测模组电池电量：{{params.battery}}</div>
				</template>
			</van-cell>
		</Block>
	</div>
</template>

<script>
	import TopBar from '@/components/TopBar'
	import Vue from 'vue'
	import Block from '@/components/Block'
	// import img from '@/assets/export.png'
	import {
		Cell,
		CellGroup,
		Field,
		DatetimePicker,
		Cascader,
		Checkbox,
		CheckboxGroup,
		Notify,    
		Toast, 
	} from 'vant'

	Vue.use(Cell);
	Vue.use(CellGroup);
	Vue.use(Field);
	Vue.use(DatetimePicker);
	Vue.use(Cascader);
	Vue.use(Checkbox);
	Vue.use(CheckboxGroup);
	Vue.use(Notify);
	Vue.use(Toast);
	export default {
		name: 'TrendAnalysis',
		components: {
			TopBar: TopBar,
			Block: Block,
		},
		data() {
			//console.log(this.id)
			return {
				id: 0,
				subject:"",
				timeType: "",
				startTime: "",
				endTime: "",
				currentDate: new Date(),
				showStart: false,
				showEnd: false,
				showDevice: false,
				showdeviceSelect: true,
				showProjectType: false,
				deviceValue: "",
				cascaderValue: "",
				options: [],
				risk: [],
				myChart: "",
				arr_x: [],
				arr_y: [],
				normalMin: "",
				normalMax: "",
				abnormalMax: "",
				abnormalMin: "",
				faultMax: "",
				faultMin: "",
				highriskMax: "",
				highriskMin: "",
				projectType: "speed",
				projectTypeValue: "速度（mm/s）",
				projectTypeOption: [
					{text:"速度（mm/s）",value:"speed"},
					{text:"加速度（m/s²）",value:"acc"},
					{text:"振幅（um）",value:"amplitude"}
				],
				projectTypeMap: {
					speed: '速度',
					acc: '加速度',
					amplitude: '振幅',
				},
				bitMap: { checkedRisk: 1, checkedFault: 2 },
				app: {},
				option: null,
				lastTimeout: null,
				timeoutSpan: 1000 * 60 * 10,
				params: {},
				equipmentId: "",
				componentId: "",
				componentType: "",
				backItem: {path:"/analysis"},
				showOptions: false,
				actions: [{ text: '频谱分析' },{ text: '检测数据一览' }],
				queryDataView:{}
			}
		},
		created() {
			//console.log(this)
			//this.id = this.$route.params.id
			let instance=this;
			console.log("设备列表页面获取传递过来的参数");
			console.log(this.$route.params)
			if(this.$route.params.backUrl){
				this.backItem = {
					path: this.$route.params.backUrl,
					name: 'DeviceList',
					params: {equipmentStatus:this.$route.params.equipmentStatus,name:this.$route.params.name}
				}
			}
			this.combest.get(this, "/company/ajaxGetComEquComZtree",  {}, function(response) {
					console.log("获取公司结构树")
					console.log(response)
					console.log(response.data)
					let zNodes = eval(response.data.data);
					instance.options = instance.formatTreeData(zNodes);
				},
				function(error) {
					console.log(error)

				})
			if(this.$route.params.componentType){
				this.showdeviceSelect = false;
				this.equipmentId = this.$route.params.equipmentId;
				this.componentId = this.$route.params.componentId;
				this.componentType = this.$route.params.componentType;
				let checkedRisk = this.risk.includes('risk');
				let checkedFault = this.risk.includes('fault');
				let riskType = (checkedRisk && this.bitMap.checkedRisk) | (checkedFault && this.bitMap.checkedFault);
				this.buildData({
					equipmentId: this.equipmentId,
					componentId: this.componentId,
					componentType: this.componentType,
					projectType: this.projectType,
					startTime: '',
					endTime: '',
					timeType: '',
					riskType: riskType,
				});
				this.ajaxQueryLatestData(this.componentId, this.componentType, this.projectType, this.equipmentId);
			}

		},
		methods: {
			back() {
				this.$router.push(this.backItem)
			},
			toggleAll() {
				if(this.risk.includes("risk") && this.risk.includes("fault") ){
					this.$refs.riskGroup.toggleAll(false);
				}else{
					this.$refs.riskGroup.toggleAll(true);	
				}
			},
			onStart(){
				let picker = this.$refs.start.getPicker();
				this.startTime = picker.getColumnValue(0) + '-' + picker.getColumnValue(1) + '-' + picker.getColumnValue(2);
				this.showStart = false;
				this.handleFilterChange();
			},
			onEnd() {
				let picker = this.$refs.end.getPicker();
				this.endTime = picker.getColumnValue(0) + '-' + picker.getColumnValue(1) + '-' + picker.getColumnValue(2);
				this.showEnd = false;
				this.handleFilterChange();
			},
			onProjectType(selected) {
				this.projectTypeValue = selected.text;
				this.projectType = selected.value;
				this.$refs.projectType.setValues([selected]);
				this.showProjectType = false;
				Toast.loading({
					message: '加载中...',
					forbidClick: true,
				});
				this.handleFilterChange();
			},
			buildData(params){
				let queryData = {
					equipmentId: params.equipmentId,
					componentId: params.componentId,
					componentType: params.componentType,
					riskType: params.riskType
				}
				if (params.projectType != '') {
					queryData.projectType = params.projectType
                }
                if (params.startTime != '') {
					queryData.strStartTime = params.startTime
                }
                if (params.endTime != '') {
					queryData.strEndTime = params.endTime
                }
                if (params.timeType != '') {
					queryData.timeType = params.timeType
                }

				var productTypeText = '速度（m/s2）';
                if (params.projectType == 'acc') {
                    productTypeText = '加速度（m/s2）';
                }
                if (params.projectType == 'amplitude') {
                    productTypeText = '振幅（um）';
				}
				if (this.componentType == 't'){
					productTypeText = '温度（℃）';
				}
				let instance=this; 
				this.combest.get(this, "/monitoringData/ajaxQueryMonDataJsonWithTemp", queryData, function(response) {
					console.log(response)
					console.log(response.data)
					let data = response.data
					instance.arr_x = [];
					instance.arr_y = [];
					for (var i = 0; i < data.length; i++) {
						instance.arr_x.push(data[i].name);
						instance.arr_y.push(data[i].value);
					}
					var module;
					instance.combest.get(instance, "/monitoringData/ajaxQueryModuleByCidAndEid", {
						cmponentId:params.componentId,
						equipmentId:params.equipmentId
						}, function(response) {
						console.log(response)
						console.log(response.data)
						let data = response.data.data
						module = data;
						if(instance.componentType == 't'){
							instance.normalMax = module.tempAbnormalMax;
							instance.normalMin = module.tempAbnormalMin;
							instance.abnormalMax = module.tempNormalMax;
							instance.abnormalMin = module.tempNormalMin;
							instance.faultMax = module.tempFaultMax;
							instance.faultMin = module.tempFaultMin;
							instance.highriskMax = module.tempHighriskMax;
							instance.highriskMin = module.tempHighriskMin;
						} else if (params.projectType == 'acc') {
							instance.normalMax = module.xAccNormalMax;
							instance.normalMin = module.xAccNormalMin;
							instance.abnormalMax = module.xAccAbnormalMax;
							instance.abnormalMin = module.xAccAbnormalMin;
							instance.faultMax = module.xAccFaultMax;
							instance.faultMin = module.xAccFaultMin;
							instance.highriskMax = module.xAccHighriskMax;
							instance.highriskMin = module.xAccHighriskMin;
						} else if (params.projectType == 'amplitude') {
							instance.normalMax = module.xAmpNormalMax;
							instance.normalMin = module.xAmpNormalMin;
							instance.abnormalMax = module.xAmpAbnormalMax;
							instance.abnormalMin = module.xAmpAbnormalMin;
							instance.faultMax = module.xAmpFaultMax;
							instance.faultMin = module.xAmpFaultMin;
							instance.highriskMax = module.xAmpHighriskMax;
							instance.highriskMin = module.xAmpHighriskMin;
						} else if (params.projectType == 'speed') {
							instance.normalMax = module.xSpeedNormalMax;
							instance.normalMin = module.xSpeedNormalMin;
							instance.abnormalMax = module.xSpeedAbnormalMax;
							instance.abnormalMin = module.xSpeedAbnormalMin;
							instance.faultMax = module.xSpeedFaultMax;
							instance.faultMin = module.xSpeedFaultMin;
							instance.highriskMax = module.xSpeedHighriskMax;
							instance.highriskMin = module.xSpeedHighriskMin;
						}
						instance.myChart.setOption(
                            (instance.option = {
                                // title: {
                                //     text: '监测分析报表',
                                // },
                                tooltip: {
                                    trigger: 'axis',
                                },
                                xAxis: {
                                    data: instance.arr_x,
                                },
                                yAxis: {
                                    splitLine: {
                                        show: false,
                                    },
                                },
                                dataZoom: [
                                    {
                                        startValue: '2020-09-01',
                                    },
                                    {
                                        type: 'inside',
                                    },
                                ],
                                visualMap: {
                                    top: 10,
                                    right: 10,
                                    precision: 2,
                                    show: false,
                                    pieces: [
                                        {
                                            gt: instance.normalMin,
                                            lte: instance.normalMax,
                                            color: 'green',
                                        },
                                        {
                                            gt: instance.abnormalMin,
                                            lte: instance.abnormalMax,
                                            color: 'yellow',
                                        },
                                        {
                                            gt: instance.highriskMin,
                                            lte: instance.highriskMax,
                                            color: 'darkorange',
                                        },
                                        {
                                            gt: instance.faultMin,
                                            color: 'red',
                                        },
                                    ],
                                    outOfRange: {
                                        color: '#999',
                                    },
                                },
                                series: {
                                    name: productTypeText,
                                    type: 'line',
                                    data: instance.arr_y,
                                    markLine: {
                                        silent: true,
                                        data: [
                                            {
                                                yAxis: instance.normalMax,
                                            },
                                            {
                                                yAxis: instance.abnormalMax,
                                            },
                                            {
                                                yAxis: instance.highriskMax,
                                            },
                                            {
                                                yAxis: instance.faultMax,
                                            },
                                        ],
                                    },
                                },
                            }),
						);
						window.addEventListener("resize", function() {
							instance.myChart.resize()
						});
					},
					function(error) {
						console.log(error)

					});
					console.log("next")
					
				},
				function(error) {
					console.log(error)

				})
			},
			//通过点击风险-高风险重新选中的数据高亮显示
			setChartHighlight(type){
				let instance = this;
				if (type == 1) {
                    instance.myChart.setOption({
                        visualMap: {
                            pieces: [
                                {
                                    gt: instance.normalMin,
                                    lte: instance.normalMax,
                                    color: '#999',
                                },
                                {
                                    gt: instance.abnormalMin,
                                    lte: instance.abnormalMax,
                                    color: '#999',
                                },
                                {
                                    gt: instance.highriskMin,
                                    lte: instance.highriskMax,
                                    color: 'darkorange',
                                },
                                {
                                    gt: instance.faultMin,
                                    color: '#999',
                                },
                            ],
                        },
                    });
                } else if (type == 2) {
                    instance.myChart.setOption({
                        visualMap: {
                            pieces: [
                                {
                                    gt: instance.normalMin,
                                    lte: instance.normalMax,
                                    color: '#999',
                                },
                                {
                                    gt: instance.abnormalMin,
                                    lte: instance.abnormalMax,
                                    color: '#999',
                                },
                                {
                                    gt: instance.highriskMin,
                                    lte: instance.highriskMax,
                                    color: '#999',
                                },
                                {
                                    gt: instance.faultMin,
                                    color: 'red',
                                },
                            ],
                        },
                    });
                } else {
                    instance.myChart.setOption({
                        visualMap: {
                            pieces: [
                                {
                                    gt: instance.normalMin,
                                    lte: instance.normalMax,
                                    color: 'green',
                                },
                                {
                                    gt: instance.abnormalMin,
                                    lte: instance.abnormalMax,
                                    color: 'yellow',
                                },
                                {
                                    gt: instance.highriskMin,
                                    lte: instance.highriskMax,
                                    color: 'darkorange',
                                },
                                {
                                    gt: instance.faultMin,
                                    color: 'red',
                                },
                            ],
                        },
                    });
                }
			},
			builCheck(){
				// 风险预警类型
                
                var checkedRisk = this.risk.includes('risk');
				var checkedFault = this.risk.includes('fault');
                var riskType = (checkedRisk && this.bitMap.checkedRisk) | (checkedFault && this.bitMap.checkedFault);
                this.setChartHighlight(riskType);
			},
			getEchartData() {
				const container = this.$refs.container
				if (container) {
					this.myChart = this.$echarts.init(container);
				}
			},
			formatTreeData(rawData){
				// type: 1企业，2设备，3部件
                var nodeType = {
                    company: 1,
                    equipment: 2,
                    component: 3,
                };

                function formatNodeList(nodeList) {
                    for (var i = 0, len = nodeList.length; i < len; i += 1) {
                        var node = nodeList[i];
						setupOption(node);
                        setupCheckbox(node);
                        setupIcon(node);

                        if (node.children && node.children.length) {
                            formatNodeList(node.children);
                        }
                    }
                }

                function setupCheckbox(node) {
                    var isModule = !!node.componentType;
                    if (!isModule) {
                        node.nocheck = true;
                    }
				}
				
				function setupOption(node) {
					node.value = node.name;
					node.text = node.name;
                }

                // 设置状态，用于设置图标
                // 一级企业、二级企业、三、设备、四部件、五模组的X/Y/轴

                // public final static int STATUS_WORKING=1;//运行（绿色）
                // public final static int STATUS_STOP=2;//停机（灰色）
                // public final static int STATUS_TAKE=3;//维保（蓝色）
                // public final static int STATUS_UNUSUAL=4;// 故障 (红色)
                // public final static int STATUS_HIGHRISK=5;//风险（橙色）
                // public final static int STATUS_FAULT=6;//异常（黄色）
                // public final static int STATUS_OFFLINE=7;//离线（棕色）
                function setupStatus(nodeList, parentStatistics) {
                    if (!nodeList || !nodeList.length) {
                        return;
                    }

                    var runningStatus = {
                        working: 1,
                        stop: 2,
                        maintain: 3,
                        abnormal: 4,
                        risk: 5,
                        fault: 6,
                        offline: 7,
                    };

                    var iconStatus = {
                        working: 'working',
                        stop: 'stop',
                        maintain: 'maintain',
                        abnormal: 'abnormal',
                        fault: 'fault',
                        risk: 'risk',
                        offline: 'offline',
                    };

                    for (var i = 0, len = nodeList.length; i < len; i += 1) {
                        var node = nodeList[i];

                        if (parentStatistics) {
                            parentStatistics.total += 1;
                        }

                        // 企业 / 部件，没有自己的状态，需根据下级的状态确定
                        if (node.type === nodeType.company || node.type === nodeType.component) {
                            var statistics = {
                                total: 0,
                                working: 0,
                                stop: 0,
                                maintain: 0,
                                abnormal: 0,
                                fault: 0,
                                risk: 0,
                                offline: 0,
                            };
                            setupStatus(node.children, statistics);

                            // offline > abnormal > fault > risk > maintain > stop > working
                            if (statistics.offline) {
                                node.status = iconStatus.offline;
                                parentStatistics && (parentStatistics.offline += 1);
                            } else if (statistics.abnormal) {
                                node.status = iconStatus.abnormal;
                                parentStatistics && (parentStatistics.abnormal += 1);
                            } else if (statistics.fault) {
                                node.status = iconStatus.fault;
                                parentStatistics && (parentStatistics.fault += 1);
                            } else if (statistics.risk) {
                                node.status = iconStatus.risk;
                                parentStatistics && (parentStatistics.risk += 1);
                            } else if (statistics.maintain) {
                                node.status = iconStatus.maintain;
                                parentStatistics && (parentStatistics.maintain += 1);
                            } else if (statistics.stop) {
                                node.status = iconStatus.stop;
                                parentStatistics && (parentStatistics.stop += 1);
                            } else {
                                node.status = iconStatus.working;
                                parentStatistics && (parentStatistics.working += 1);
                            }
                        }
                        // 设备 / 模组
                        else if (node.type === nodeType.equipment || node.componentType) {
                            setupStatus(node.children);

                            var statusField = node.type === nodeType.equipment ? 'equipmentStatus' : 'moduleStatus';

                            if (node[statusField] === runningStatus.working) {
                                node.status = iconStatus.working;
                                parentStatistics && (parentStatistics.working += 1);
                            } else if (node[statusField] === runningStatus.stop) {
                                node.status = iconStatus.stop;
                                parentStatistics && (parentStatistics.stop += 1);
                            } else if (node[statusField] === runningStatus.maintain) {
                                node.status = iconStatus.maintain;
                                parentStatistics && (parentStatistics.maintain += 1);
                            } else if (node[statusField] === runningStatus.abnormal) {
                                node.status = iconStatus.abnormal;
                                parentStatistics && (parentStatistics.abnormal += 1);
                            } else if (node[statusField] === runningStatus.risk) {
                                node.status = iconStatus.risk;
                                parentStatistics && (parentStatistics.risk += 1);
                            } else if (node[statusField] === runningStatus.fault) {
                                node.status = iconStatus.fault;
                                parentStatistics && (parentStatistics.fault += 1);
                            } else if (node[statusField] === runningStatus.offline) {
                                node.status = iconStatus.offline;
                                parentStatistics && (parentStatistics.offline += 1);
                            }
                        }
                    }
                }

                // 设置节点图标
                function setupIcon(node) {
                    switch (node.type) {
                        case nodeType.company:
                            node.icon = '/img/monitor/ic_company_' + node.status + '.png';
                            break;
                        case nodeType.equipment:
                            node.icon = '/img/monitor/ic_device_' + node.status + '.png';
                            break;
                        default:
                            node.icon = '/img/monitor/ic_device_' + node.status + '.png';
                    }
                }

                setupStatus(rawData);
                formatNodeList(rawData);
				
                return rawData;
			},
			handleFilterChange(){
				// 风险预警类型
				var checkedRisk = this.risk.includes('risk');
				var checkedFault = this.risk.includes('fault');
				var riskType = (checkedRisk && this.bitMap.checkedRisk) | (checkedFault && this.bitMap.checkedFault);
				console.log(riskType)
				if (this.timeType != '' && this.startTime != '') {
					Notify({ type: 'warning', message: '不能同时选择时间类型和时间段' });
					return false;
				}
				if (this.timeType != '' && this.endTime != '') {
					Notify({ type: 'warning', message: '不能同时选择时间类型和时间段' });
					return false;
				}
				if (this.projectType == null || this.projectType == '') {
					Notify({ type: 'warning', message: '请选择查询项目' });
					return false;
				}
				let componentId = this.componentId;
				let componentType = this.componentType;

				if (componentId == '') {
					Notify({ type: 'warning', message: '请选择部件X/Y/Z轴' });
					return false;
				}
				if (componentType == '') {
					Notify({ type: 'warning', message: '请选择部件X/Y/Z轴' });
					return false;
				}
				var equipmentId = this.equipmentId;

				this.buildData({
					equipmentId: equipmentId,
					componentId: componentId,
					componentType: componentType,
					projectType: this.projectType,
					startTime: this.startTime,
					endTime: this.endTime,
					timeType: this.timeType,
					riskType: riskType,
				});
				//后下角显示数据
				this.ajaxQueryLatestData(componentId, componentType, this.projectType, equipmentId);
				var slices = this.deviceValue.split(' > ');
				slices[slices.length - 1] = this.projectTypeMap[this.projectType];
				this.deviceValue = slices.join(' > ');
			},
			getParentEquipmentId(moduleNode,selectedOptions){
				if (!moduleNode) {
                    return '';
                }
				if(moduleNode.index < 2){
					return '';
				}
                var componentNode = selectedOptions[moduleNode.index-1];

                if (!componentNode) {
                    return '';
                }

                var equipmentNode = selectedOptions[moduleNode.index-2];

                return (equipmentNode && equipmentNode.id) || '';
			},
			onFinish({ selectedOptions }) {
				this.showDevice = false;
				this.deviceValue = selectedOptions.map((option) => option.text).join(' > ');
				let checkedNode = selectedOptions[selectedOptions.length-1];
				this.componentId = checkedNode.id;
				this.componentType = checkedNode.componentType;
				checkedNode.index = selectedOptions.length-1;
				console.log(selectedOptions)
                if (checkedNode) {
                    if (checkedNode.componentType == undefined) {
						console.log("no")
                    } else {
                        var em = this.projectTypeMap['speed'];
						this.deviceValue = this.deviceValue + ' > ' + em;

                        var checkedRisk = this.risk.includes('risk');
                        var checkedFault = this.risk.includes('fault');
                        var riskType = (checkedRisk && this.bitMap.checkedRisk) | (checkedFault && this.bitMap.checkedFault);
						var equipmentId = this.getParentEquipmentId(checkedNode,selectedOptions);
						this.equipmentId = equipmentId;
                        this.buildData({
                            equipmentId: equipmentId,
                            componentId: checkedNode.id,
                            componentType: checkedNode.componentType,
                            projectType: 'speed',
                            startTime: '',
                            endTime: '',
                            timeType: '',
                            riskType: riskType,
                        });
                        this.ajaxQueryLatestData(checkedNode.id, checkedNode.componentType, 'speed', equipmentId);
                    }
                } else {
                    return false;
                }
			},
			ajaxQueryLatestData(componentId, componentType, projectType, equipmentId, instance){
				if(instance == null){
					instance = this;
				}
				// 定时请求
				window.clearTimeout(instance.lastTimeout);
				
				instance.lastTimeout = window.setTimeout(function () {
                    instance.ajaxQueryLatestData(componentId, componentType, projectType, equipmentId, instance);
				}, instance.timeoutSpan);
				instance.combest.get(instance, "/monitoringData/ajaxQueryLatestData", {
						componentId: componentId,
						componentType: componentType,
						projectType: projectType,
						equipmentId: equipmentId
					},
					function(response) {
						console.log("获取返回值")
						console.log(response.data.data)
						if(response.data.code == 0){
							let data = response.data.data
							console.log(data)
							instance.params = data;
						}
						
					},
					function(error) {
						console.log(error)
					});
			},
			onSelect(action) {
				let instance = this;
				if(action.text == '频谱分析'){
					console.log(instance.$route.params)
					this.$router.push({
						name:'WaveAnalysis',
						params:{
							queryDataView : instance.$route.params
							},
						})
				}
				if(action.text == '检测数据一览'){
					console.log("点击了检测数据一览的按钮")
					console.log(instance.$route.params)
					this.$router.push({
						name:'DataOverview',
						params:{
							queryDataView : instance.$route.params
							},
						})
				}
			},
		},
		mounted(){
			this.getEchartData()  
		},
	}
</script>

<style>
	.timeIcon{
		margin-top: 7px;
		font-size: 26px !important;
	}
</style>
