<template>
	<div id="deptList" class="content">
		<TopBar :title='title' @click-left="back">

		</TopBar>
		<van-cell-group>
			<van-cell :title="desc" value="时间" />
			<van-cell v-for="(item,i) in list" :key="i" :title="item.value" size="mini" style="border-bottom: 1px solid #ccc!important;padding: 14px;border-color: #ccc!important;margin-top: 1px;margin-left: 2px;">
				<template #default>
					<div>{{item.name}}</div>
				</template>
			</van-cell>
		</van-cell-group>



	</div>
</template>

<script>
	import TopBar from '@/components/TopBar'
	import Vue from 'vue';
	import {
		List
	} from 'vant';

	Vue.use(List);
	export default {
		name: "DataOverview",
		components: {
			TopBar: TopBar,
		},
		data() {
			return {
				list: [],
				loading: false,
				finished: false,
				refreshing: false,
				page: 1,
				limit: 50,
				queryDataView: {},
				title:"",
				desc:"前端速度(mm/s)"

			};
		},
		created() {
			let instance = this;
			console.log("获取传递过来的参数")
			console.log(this.$route.params.queryDataView);
			if(this.$route.params.queryDataView.componentType == "t"){
				this.desc = "温度（℃）"
			}
			console.log("发起数据请求");
			this.queryDataView = this.$route.params.queryDataView;
			this.queryDataView.componentName = this.$route.params.queryDataView.componentName + this.$route.params.queryDataView.suffix
			this.title = this.queryDataView.componentName;
			this.combest.get(this, "/monitoringData/ajaxQueryMonDataJsonWithTemp", instance.queryDataView,
				function(response) {
					console.log("获取返回值");
					console.log(response.data);
					instance.list.push.apply(instance.list, response.data);
				},
				function(error) {
					console.log(error)
				})
				
		},

		methods: {

			back() {
				this.$router.push({
					name:"TrendAnalysis",
					params:{
						componentType:this.queryDataView.componentType,
						equipmentId:this.queryDataView.equipmentId,
						componentId:this.queryDataView.componentId,
						backUrl:'/monitoring/deviceList',
						name:this.queryDataView.name,
						equipmentStatus:this.queryDataView.equipmentStatus,
						componentName: this.queryDataView.componentName.split("_")[0],
						suffix:this.queryDataView.suffix,
					}
				})
			},
		},
	};
</script>

