<template>
	<div id="deptList" class="content">
		<TopBar title='申请列表' @click-left="back">
			<!--<template slot="right"></template> 支持title,right-->
		</TopBar>

		<van-pull-refresh v-model="refreshing" @refresh="onRefresh">
			<van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
				<van-cell v-for="(item,i) in list" :key="i" :title="item.subject" is-link :to="{path:'/test/test',name:'Test',params:{id:item.id}}" />
			</van-list>
		</van-pull-refresh>
		
	</div>
</template>

<script>
	import TopBar from '@/components/TopBar'

	import Vue from 'vue';
	import {
		List
	} from 'vant';

	Vue.use(List);
	export default {
		name: "testList",
		components: {
			TopBar: TopBar,
			
		},
		data() {
			return {
				list: [],
				loading: false,
				finished: false,
				refreshing: false,
				page: 1,
				limit:50,
				data: {}
			};
		},

		methods: {
			back() {

				this.$router.push({path:"/home"})
			},
			onLoad() {
				if (this.refreshing) {
					this.list = [];
					this.refreshing = false;
				}
				let instance = this;
				this.combest.list(this, "/approval/list",this.page, this.limit, this.data, function(response) {
						//instance.list.push(response.data.data);
						instance.list.push.apply(instance.list, response.data.data);
						
						instance.loading = false;
						
						
						if(response.data.data.length<1){
							instance.page--;
							instance.finished = true;
						}else{
							instance.page++;
						}
					},
					function(error) {
						console.log(error)

					})

			},
			onRefresh() {
				// 清空列表数据
				this.finished = false;

				// 重新加载数据
				// 将 loading 设置为 true，表示处于加载状态
				this.loading = true;
				this.onLoad();
			},
		},
	};
</script>

