<template>
	<div id='topBar'>
	<van-nav-bar
	:title="title"
	:left-text="leftText"
	:right-text="rightText"
	:left-arrow=canBack
	@click-left="topBarClickLeft"
	@click-right="topBarClickRight"
	fixed
	>
	<template #left>
		<slot name="left"></slot>
	</template>
	<template #title>
		<slot name="title"></slot>
	</template>
	<template #right>
		<slot name="right"></slot>
	</template>
	</van-nav-bar>
	</div>
</template>

<script>
	export default{
		name:'TopBar',
		props:{
			title:{
				type:String,
				required:true
			},
			leftText:{
				type:String,
				required:false,
				default:'返回'
			},
			canBack:{
				type:Boolean,
				required:false,
				default:true
			},
			rightText:{
				type:String,
				required:false
			}
		},
		methods:{
			topBarClickLeft(){
				this.$emit("click-left");
				//console.log(this.$emit());
				//this.$router.go(-1)
			},
			topBarClickRight(){
				
				this.$emit("click-right");
				//console.log(this.$emit());
				//this.$router.go(-1)
			}
		}
	}
</script>

<style>
</style>
