export const Combest = {

	//默认的返回事件
	back:function(vue){
		vue.$router.go(-1);
	},
	/**显示数据
	 * vue对象
	 * url，请示地址
	 * page:页数
	 * limit:每页显示条数
	 * data:请求参数 
	 */
	list: function(vue, url, page, limit, data, sucess, error) {
		let token = Combest.createAuthority(vue);
		if (data == null) {
			data = {};
		}
		var params = {
			page: page,
			limit: limit
		}
		for (var r in data) {
			params[r] = data[r]
		}
		vue.$axios.get(url, {
			params: params,
			headers: {
				'appAuthorization': token
			} //设置header信息
		}).then(function(res) {
			//console.log(res)
			sucess(res)
		}).catch(function(err) { // 请求失败处理

			error(err)
		})
	},
	listPost : function(vue, url, page, limit, data, sucess, error, contentType) {
		let token = Combest.createAuthority(vue);
		if (data == null) {
			data = {};
		}
		var params = {
			page: page,
			limit: limit
		}
		for (var r in data) {
			params[r] = data[r]
		}
		let headers = {
			'appAuthorization': token
		}
		if (contentType) {
			headers["Content-Type"] = contentType
		} else {
			headers["Content-Type"] = "application/x-www-form-urlencoded; charset=UTF-8"
		}
		vue.$axios.post(url, params, {
			headers: headers,
		}).then(function(res) {
			console.log(res)
			sucess(res)
		}).catch(function(err) { // 请求失败处理
		
			error(err)
		})
	},
	get: function(vue, url, data, sucess, error) {
		let token = Combest.createAuthority(vue);
		if (data == null) {
			data = {};
		}
		var params = {}
		for (var r in data) {
			params[r] = data[r]
		}
		vue.$axios.get(url, {
			params: params,
			headers: {
				'appAuthorization': token
			} //设置header信息
		}).then(function(res) {
			//console.log(res)
			sucess(res)
		}).catch(function(err) { // 请求失败处理

			error(err)
		})
	},
	getAllList: function(vue, url, data, sucess, error){
		let token = Combest.createAuthority(vue);
		if (data == null) {
			data = {};
		}
		var params = {}
		for (var r in data) {
			params[r] = data[r]
		}
		vue.$axios.get(url, {
			params: params,
			headers: {
				'appAuthorization': token
			} //设置header信息
		}).then(function(res) {
			//console.log(res)
			sucess(res)
		}).catch(function(err) { // 请求失败处理
		
			error(err)
		})
	},
	post: function(vue, url, data, succes, error, contentType) {
		let token = Combest.createAuthority(vue);
		let headers = {
			'appAuthorization': token
		}
		if (contentType) {
			headers["Content-Type"] = contentType
		}
		vue.$axios.post(url, data, {
			headers: headers,
		}).then(function(res) {
			console.log(res)
			succes(res)
		}).catch(function(err) { // 请求失败处理

			error(err)
		})
	},
	syncGetAllList:async function(vue, url, data, sucess, error){
		let token;
		await Combest.syncCreateAuthority(vue).then(res => {
			token = res;
		});
		if (data == null) {
			data = {};
		}
		var params = {}
		for (var r in data) {
			params[r] = data[r]
		}
		return await vue.$axios.get(url, {
			params: params,
			headers: {
				'appAuthorization': token
			} //设置header信息
		}).then(function(res) {
			//console.log(res)
			sucess(res)
			// return res;
		}).catch(function(err) { // 请求失败处理
			error(err)
		});
	},
	createAuthority: function(vue) {
		let appToken = vue.$store.getters.getAppToken;
		let appStamp = vue.$store.getters.getStamp;
		let appKey = vue.$store.getters.getAppKey;
		let appSec = vue.$store.getters.getAppSec;
		var appSignSec = "appSec=" + appSec + "&stamp=" + appStamp;
		var sec = vue.$CryptoJS.HmacSHA256(appSignSec, "combest_!X@#$##$");
		var stamp = (new Date()).getTime();
		var query = "appKey=" + appKey + "&appToken=" + appToken + "&stamp=" + stamp;
		var sign = vue.$CryptoJS.HmacSHA256(query, "" + sec);
		query += "&token=" + sign;
		query = vue.$CryptoJS.enc.Utf8.parse(query);

		return vue.$CryptoJS.enc.Base64.stringify(query);
	},
	syncCreateAuthority:async function(vue) {
		let appToken = vue.$store.getters.getAppToken;
		let appStamp = vue.$store.getters.getStamp;
		let appKey = vue.$store.getters.getAppKey;
		let appSec = vue.$store.getters.getAppSec;
		var appSignSec = "appSec=" + appSec + "&stamp=" + appStamp;
		var sec = vue.$CryptoJS.HmacSHA256(appSignSec, "combest_!X@#$##$");
		var stamp = (new Date()).getTime();
		var query = "appKey=" + appKey + "&appToken=" + appToken + "&stamp=" + stamp;
		var sign = vue.$CryptoJS.HmacSHA256(query, "" + sec);
		query += "&token=" + sign;
		query = vue.$CryptoJS.enc.Utf8.parse(query);
	
		return vue.$CryptoJS.enc.Base64.stringify(query);
	},
	/**
	 * @param {Object} str
	 * 字符串去空格
	 */
	trim:function (str) {
		return str.replace(/(^\s*)|(\s*$)/g, "");
	},
	/**
	 * 获取当前时间
	 */
	getCurrentTime: function () {
		var date = new Date();
		var year = date.getFullYear();
		var month = (date.getMonth() + 1) < 10 ? "0" + (date.getMonth() + 1) : (date.getMonth() + 1);
		var day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
		var hour = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
		var minute = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
		var second = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
		return year + "-" + month + "-" + day + " " + hour + ":" + minute + ":" + second;
	},
	/**
	 * 获取当前日期
	 */
	getCurrentDate: function () {
		var date = new Date();
		var year = date.getFullYear();
		var month = (date.getMonth() + 1) < 10 ? "0" + (date.getMonth() + 1) : (date.getMonth() + 1);
		var day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
		return year + "-" + month + "-" + day;
	},
	/**
	 * @param {Object} str
	 * 判断字符串是否为空
	 */
	isEmpty: function (str){
		return (str == null || str == '');
	}
}
