<template>
	<div class="main">
		<van-nav-bar title="用户登录" class="title" />
		<van-form @submit="login" id="login">
			<van-row type="flex" justify="center" class="img">
				<van-col span="1">&nbsp;</van-col>
				<van-col span="22"><img src="../assets/zrhz.png" /></van-col>
				<van-col span="1">&nbsp;</van-col>
			</van-row>

			<van-row type="flex" justify="center" class="line" v-if="loginMethod!='phone'">
				<van-col span="3">&nbsp;</van-col>
				<van-col span="18" style="margin-top:20px;">
					<van-cell-group>
						<van-field placeholder="请输入用户名" type="text" v-model="user.account" left-icon="manager" :rules="[{ required: true }]" />
					</van-cell-group>
					<van-cell-group>
						<van-field placeholder="请输入密码" type="password" v-model="user.password" left-icon="lock" :rules="[{ required: true }]" />
					</van-cell-group>
				</van-col>
				<van-col span="3">&nbsp;</van-col>
			</van-row>

			<van-row type="flex" justify="center" class="line" v-else>
				<van-col span="3">&nbsp;</van-col>
				<van-col span="18" style="margin-top:20px;">
					<van-cell-group>
						<van-field v-model="userByPhone.mobile" placeholder="请输入手机号" left-icon="phone" :rules="[{ validator,message: '手机号格式错误' }]" type="tel"/>
					</van-cell-group>
					<van-cell-group >
						<van-field v-model="userByPhone.validateCode" center clearable placeholder="请输入短信验证码" left-icon="envelop-o" :rules="[{ required: true }]">
						<template #button v-if="loginMethod=='phone'">
							<van-button size="small" type="primary" @click="getValidateCode" native-type="button">获取验证码</van-button>
						</template>
						</van-field>
					</van-cell-group>
				</van-col>
				<van-col span="3">&nbsp;</van-col>
			</van-row>

			<van-row type="flex" justify="center" class="btns" style="margin-top:20px;">
				<van-col span="3">&nbsp;</van-col>
				<van-col span="18">
					<van-button type="primary" size="large" color="#177CB0" native-type="submit">登录</van-button>
				</van-col>
				<van-col span="3">&nbsp;</van-col>
			</van-row>
			<van-row type="flex" justify="center" class="line">
				<van-col span="15">&nbsp;</van-col>
				<van-col span="9" style="margin:20px 0">
					<van-checkbox v-model="autoLogin" shape="square">自动登陆</van-checkbox>
				</van-col>
			</van-row>
			
		</van-form>
		<van-row align="bottom" type="flex" justify="center">
			<van-col span="5" v-show="loginMethod=='phone' || loginMethod=='account'">
				<van-icon name="phone-circle-o" size="34px" color="#177CB0" @click="loginMethod=loginMethod=='phone'?'account':'phone';loginInfo=loginMethod=='phone'?'账号密码登录':'手机登录'"/>
			</van-col>
			<van-col span="5" v-show="loginMethod=='wechat' || loginMethod=='account'" @click="loginByWeChat">
				<van-icon name="chat-o" size="34px" color="#177CB0"  />
			</van-col>
			
			
		</van-row>
		<van-row align="bottom" type="flex" justify="center">
			<van-col span="5" style="color:#969799;font-size:12px" v-show="loginMethod=='phone' || loginMethod=='account'">{{loginInfo}}</van-col>
			<van-col span="5" style="color:#969799;font-size:12px" v-show="loginMethod=='wechat' || loginMethod=='account'" >{{loginInfoWechat}}</van-col>
		</van-row>
	</div>
</template>

<script>
	import Vue from 'vue'
	import {
		Form,
		Toast,
		Checkbox
	} from 'vant'
	import CryptoJS from 'crypto-js'
	import Qs from 'qs'

	Vue.use(Toast)
	Vue.use(Form)
	Vue.use(Checkbox)
	var qyweixin = require('@/js/qyweixin.js')
	export default {
		name: 'Login',
		data() {
			let user = {
				account: "",
				password: ""
			};

			let account = localStorage.getItem('account');
			if (account) {
				user.account = account;
			}
			let password = localStorage.getItem('password');
			if (password) {
				user.password = CryptoJS.enc.Base64.parse(localStorage.getItem('password')).toString(CryptoJS.enc.Utf8);
			}

			let userByPhone = {
				mobile: "",
				validateCode: "",
			}
			
			let mobile = localStorage.getItem('mobile');
			if (mobile) {
				userByPhone.mobile = mobile;
			}

			let autoLogin = localStorage.getItem('autoLogin');
			if (autoLogin == true || autoLogin == "true") {
				autoLogin = true;
			} else {
				autoLogin = false;
			}

			return {
				user: user,
				autoLogin: autoLogin,
				loginMethod: "account",
				userByPhone:userByPhone,
				loginInfo: "手机登录",
				loginInfoWechat: "微信登录",
				qyweixin: qyweixin
			}
		},
		created() {
			//console.log(this.$store)
			//console.log(this.$store.getters.getProtocol+"://"+this.$store.getters.getServer+":"+this.$store.getters.getPort)

			if (this.autoLogin == true || this.autoLogin == "true") {
				if (localStorage.getItem("account") && localStorage.getItem("password")) {
					this.login();
				}
			}
			
		},
		mounted() {
			//this.combest.alert(this)
		},
		methods: {
			
			login() {
				const instance = this
				// const account = this.account
				// const password = this.password
				const loginToast = Toast.loading({
					message: '登陆中...',
					forbidClick: true
				})

				if(this.loginMethod=='phone'){
					console.log("")
					this.$axios
						.post('/iotapp/user/loginByMobile', Qs.stringify(this.userByPhone))
						.then(function(response) {
							loginToast.clear()
							const loginMessage = response.data
							console.log(loginMessage)
							if (loginMessage.data) {
								instance.$store.commit("setAppToken", loginMessage.data.token);
								instance.$store.commit("setStamp", loginMessage.data.stamp)
								instance.$store.commit("setUser", loginMessage.data)
								localStorage.setItem('phone', instance.phoneNum)
								console.log(instance.autoLogin)
								localStorage.setItem('autoLogin', instance.autoLogin)
								sessionStorage.currentUser = loginMessage.data
								instance.$router.push('/home')
							} else {
								Toast('登陆失败:' + loginMessage.msg);
							}
						})
						.catch(function(error) { // 请求失败处理
							loginToast.clear()
							Toast('登陆失败!')
							console.log(error)
						})
				}else if(this.loginMethod=='account'){
					// return false
					let password = this.user.password;
					this.user.password = CryptoJS.enc.Base64.stringify(CryptoJS.enc.Utf8.parse(password))
					// const loginUser = this.user
					this.$axios
						.post('/login', Qs.stringify(this.user))
						.then(function(response) {
							loginToast.clear()
							const loginMessage = response.data
							console.log(loginMessage)
							if (loginMessage.data) {
								console.log(loginMessage.user.stamp)
								instance.$store.commit("setAppToken", loginMessage.user.token);
								instance.$store.commit("setStamp", loginMessage.user.stamp)
								instance.$store.commit("setUser", loginMessage.user)
								localStorage.setItem('account', instance.user.account)
								localStorage.setItem('password', instance.user.password)
								console.log(instance.autoLogin)
								localStorage.setItem('autoLogin', instance.autoLogin)
								sessionStorage.currentUser = loginMessage.user
								instance.$router.push('/home')
							} else {
								Toast('登陆失败:' + loginMessage.msg);
								instance.user.password = password;
							}
						})
						.catch(function(error) { // 请求失败处理
							instance.user.password = password;
							loginToast.clear()
							Toast('登陆失败!')
							console.log(error)
						})
				}
			},
			/* eslint-disable */
			loginByWeChat(){
				console.log("enrer");
				let instance = this;
				// instance.getUserInfo('KAGDEKMkXHTp6yzHlniupvNShX9pUWPisFOjfE0N5ho');
				if (!window.plus) return;
				plus.oauth.getServices(function(services){
					var qyweixin;
					for(var i in services){
						var service=services[i];
						console.log(JSON.stringify(service));
						if(service.id == "qyweixin"){
							qyweixin = service;
							break;
						}
					}
					if(qyweixin){
						qyweixin.login(function(){
							// Toast(JSON.stringify(qyweixin.authResult));
							instance.getUserInfo(qyweixin.authResult);
						},function(e){
							plus.nativeUI.alert("详情错误信息请参考授权登录(OAuth)规范文档：http://www.html5plus.org/#specification#/specification/OAuth.html",null,"登录失败["+e.code+"]："+e.message);
						});
					}
				},function(e){
					Toast('获取登录认证失败：'+e.message);
				});
			},
			/* eslint-disable */
			validator(val){
				if(val == '' || val == null){
					return false;
				}
				return !/^1[3456789]d{9}$/.test(val)
			},
			getValidateCode(){
				if(!this.validator(this.userByPhone.mobile)){
					Toast.fail('请输入正确的手机号');
					return false;
				}
				this.$axios
					.post('/iotapp/user/sendLoginSmsCode', Qs.stringify({mobile:this.userByPhone.mobile}))
					.then(function(response) {
						console.log(response)
						if(response.data.code == 0){
							Toast("验证码已发送!")
						}else{
							Toast('获取验证码失败!')
							console.log(response.data.message)
						}
					})
					.catch(function(error) { // 请求失败处理
						Toast('获取验证码失败!')
						console.log(error)
					})
			},
			getUserInfo(code){
				const loginToast = Toast.loading({
					message: '登陆中...',
					forbidClick: true
				})
				this.$axios
					.post('/iotapp/wechat/login', Qs.stringify({code:code}))
					.then(function(response) {
						loginToast.clear();
						console.log(response)
						const loginMessage = response.data
						console.log(loginMessage)
						if (loginMessage.data) {
							instance.$store.commit("setAppToken", loginMessage.data.token);
							instance.$store.commit("setStamp", loginMessage.data.stamp)
							instance.$store.commit("setUser", loginMessage.data)
							localStorage.setItem('account', loginMessage.data.account)
							localStorage.setItem('password', loginMessage.data.password)
							console.log(instance.autoLogin)
							localStorage.setItem('autoLogin', instance.autoLogin)
							sessionStorage.currentUser = loginMessage.data
							instance.$router.push('/home')
						} else {
							Toast('登陆失败:' + loginMessage.msg);
						}
					})
					.catch(function(error) { // 请求失败处理
						loginToast.clear()
						Toast('微信登录失败!')
						console.log(error)
					})
			},
		}
	}
</script>

<style>
	
