<template>
	<div id="sysMessageInfo" class="content">
		<TopBar title='系统消息详情' @click-left="back"  @click-right="scan">
			<template #right>
				<van-icon name="scan" size="18" class="leftBar" />
			</template>
		</TopBar>
		<div>
			<van-cell :value="title" size="large" style="font-size: 16px;font-weight: bolder;" />
			<div style="color: #323233;text-align: center; font-size: 12px;line-height: 24px;margin-bottom: 20px;">时间:{{createTime}}</div>
			<van-field v-model="description" rows="1" autosize label="" readonly type="textarea"  />
		</div>
		<TagBar></TagBar>

	</div>
</template>

<script>
	import TopBar from '@/components/TopBar'
	import TagBar from '@/components/TagBar'
	import Vue from 'vue'
	import {
		Cell,
		CellGroup,
		Toast,
		Field,
		Notify, 
	} from 'vant'

	Vue.use(Cell)
	Vue.use(CellGroup)
	Vue.use(Toast)
	Vue.use(Field)
	Vue.use(Notify);
	export default {
		name: 'SysMessageInfo',
		components: {
			TopBar: TopBar,
			TagBar: TagBar
		},
		data() {
			//console.log(this.id)
			return {
				id: 0,
				data:"",
				title:"",
				description:"",
				userId:"",
				workOrderId:"",
				createTime:"",
				readed:"",
			}
		},
		created() {

			let instance = this;
			this.combest.get(this, "/iotapp/message/querySysMsgById", {
					id: this.$route.params.id
				}, function(response) {
					console.log(response)
					console.log("获取返回值")
					console.log(response.data.data)
					console.log("获取返回值")
					Object.assign(instance, response.data.data)
					console.log(instance)
					if(response.data.data!=null && response.data.data.readed == 0){
						//更新这条信息的状态，将未读状态标志未已读状态
						instance.updateSysMsgReadStatus();
					}
					
				},
				function(error) {
					console.log(error)
				})

		},
		
		methods: {
			
			onSubmit() {
				
			},
			back() {
				this.$router.push({
					name:'Message',
					params:{
						active:"系统",
					}
				})
			},
			updateSysMsgReadStatus(){
				console.log("准备更新消息的已读状态")
				this.combest.get(this, "/iotapp/message/updateSysMsgReadStatus", {
						id: this.$route.params.id
					}, function(response) {
						console.log(response)
						console.log("获取返回值")
						console.log(response.data.data)
						console.log("获取返回值")
					},
					function(error) {
						console.log(error)
					})
			},
			scan() {
				console.log("点击了扫码");
				let instance = this;
				this.$router.push({path:"/moudules/scanQRCode",name:"ScanQRCode",params:{
					fun:function(vue,result){
						// alert(result)
						// alert(vue.$route.params.subject)
						console.log("发起了扫描二维码");
						console.log("result");
						console.log(result);
						console.log(JSON.stringify(vue.$route.params));
						console.log(vue.$route.params.subject);
						//判断该工单上的模组编码和扫描获得到的模组编码是否一致
						if(instance.title.indexOf(result)!= -1){
							instance.combest.get(instance, "/iotapp/workOrder/isModuleWorkOrder", {
									moduleCode: instance.$route.params.result
								}, function(response) {
									console.log("判断该模组所属工单是否属于当前用户")
									console.log(JSON.stringify(response.data.data))
									if(response.data.data == true){
										Notify({ type: 'success', message: '该模组编码和工单上的模组编码一致' });
										vue.$router.push({path: "/scan/WorkOrderInformation", name:"WorkOrderInformation",params:{result:result,from:'sysMessageInfo',sysMessageId:instance.id}})
									}else{
										Notify({ type: 'warning', message: '该工单不属于该用户，没有权限查看' });
										console.log("该工单不属于该用户时>>"+instance.id)
										vue.$router.push({
											path: "/main", 
											name:"SysMessageInfo",
											params:{
												id:instance.id
											}
										})
									}
								},
								function(error) {
									console.log(error)
							})
						}else{
							Notify({ type: 'warning', message: '模组编码和设备档案工单上的模组编码不一致，扫描获得的模组编码为：【'+result+'】' });
							console.log("模组编码不一致时>>"+instance.id)
							vue.$router.push({
								path: "/main", 
								name:"SysMessageInfo",
								params:{
									id:instance.id
								}
							})
						}
						
						
					},
					backUrl:"/main",
					subject:"测试"
				}})
			},
			
		},
	}
</script>

