<template>
  <div id="my" class="content">
    <van-nav-bar title="我" fixed @click-right="scan">
   
		<template #right>
			<van-icon name="scan" size="18" class="leftBar" />
		</template>
   
    </van-nav-bar>
    <van-cell title="姓名" :value="name"  />
    <van-cell title="帐号" :value="account"  />
	<van-cell title="个人设置" is-link to="/me/userSetting"  />
	<van-cell title="推荐我们" is-link to="/me/recommend"  />
	<van-cell title="切换工厂" is-link to="/me/choiseCompany"  />
    <van-cell title="退出帐号"  @click="logout" />
    <van-cell title="关于我们" is-link to="/me/about"  />
    <TagBar></TagBar>
  </div>
</template>

<script>
import TagBar from '@/components/TagBar'
import Vue from 'vue'
import { Cell, CellGroup } from 'vant'

Vue.use(Cell)
Vue.use(CellGroup)
export default{
  name: 'My',
  components: {TagBar: TagBar},
  data(){
    return this.$store.getters.getUser
  },
  methods:{
    logout(){
      localStorage.clear();
      this.$router.push({path:"/login"})
    },
	out(){
		/* eslint-disable */
		 plus.runtime.quit();
		 /* eslint-disable */
	},
	scan() {
		let instance = this;
		this.$router.push({path:"/moudules/scanQRCode",name:"ScanQRCode",params:{
			fun:function(vue,result){
				// alert(result)
				// alert(vue.$route.params.subject)
				console.log("发起了扫描二维码");
				console.log("result");
				console.log(result);
				console.log(JSON.stringify(vue.$route.params));
				console.log(vue.$route.params.subject);
				instance.combest.get(instance, "/iotapp/workOrder/isModuleWorkOrder", {
						moduleCode: instance.$route.params.result
					}, function(response) {
						console.log("判断该模组所属工单是否属于当前用户")
						console.log(JSON.stringify(response.data.data))
						if(response.data.data == true){
							vue.$router.push({path: "/scan/WorkOrderInformation", name:"WorkOrderInformation",params:{result:result,from:'my'}})
						}else{
							Toast.setDefaultOptions({ duration: 1500 });
							Toast.fail('该工单不属于该用户，没有权限查看');
							vue.$router.push({path: "/my", name:"My"})
						}
					},
					function(error) {
						console.log(error)
				})
				
			},
			backUrl:"/main",
			subject:"测试"
		}})
	},
  }
}
</script>

