import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/Login'
import Home from '@/views/Home.vue'
import Main from '@/views/Main.vue'
import My from '@/views/me/My.vue'
import Password from '@/views/me/Password.vue'
import TestList from '@/views/test/TestList.vue'
import Test from '@/views/test/Test.vue'
import ScanQRCode from '@/views/moudules/ScanQRCode.vue'
import About from '@/views/me/About.vue'
import UserSetting from '@/views/me/UserSetting.vue'
import Recommend from '@/views/me/Recommend.vue'
import ChoiseCompany from '@/views/me/ChoiseCompany.vue'
import ChoiseSecondCompany from '@/views/me/ChoiseSecondCompany.vue'
import ChoiseWorkShop from '@/views/me/ChoiseWorkShop.vue'
import ChoiseWorkStation from '@/views/me/ChoiseWorkStation.vue'
import WorkOrderInformation from '@/views/scan/WorkOrderInformation.vue'
import WaveAnalysis from '@/views/monitoring/WaveAnalysis.vue'
import DataOverview from '@/views/monitoring/DataOverview.vue'
import DeviceList from '@/views/monitoring/DeviceList.vue'
import Message from '@/views/Message.vue'
import SysMessageInfo from '@/views/message/SysMessageInfo.vue'
import EventMessageInfo from '@/views/message/EventMessageInfo.vue'

import TrendAnalysis from '@/views/monitoring/TrendAnalysis.vue'
import Analysis from '@/views/Analysis.vue'
const originalPush = Router.prototype.push;
Router.prototype.push = function(location) {
	return originalPush.call(this, location).catch(err => err)
}
Vue.use(Router)

export default new Router({
	routes: [{
			path: '/login',
			name: 'Login',
			component: Login
		},
		{
			path: '/home',
			name: 'Home',
			component: Home
		},
		{
			path: '/main',
			name: 'Main',
			component: Main,
			children:	[
				{
					// 当 /main/choiseCompany 匹配成功，
					// UserProfile 会被渲染在 User 的 <router-view> 中
					path: 'choiseCompany',
					name: 'MainChoiseCompany', 
					component: ChoiseCompany
				}
			]
		},
		{
			path: '/me/my',
			name: 'My',
			component: My
		},
		{
			path: '/message',
			name: 'Message',
			component: Message
		},
		{
			path: '/monitoring/waveAnalysis',
			name: 'WaveAnalysis',
			component: WaveAnalysis
		},
		{
			path: '/monitoring/dataOverview',
			name: 'DataOverview',
			component: DataOverview
		},
		{
			path: '/me/password',
			name: 'Password',
			component: Password
		},
		{
			path: '/test/testList',
			name: 'TestList',
			component: TestList
		},
		{
			path: '/test/test',
			name: 'Test',
			component: Test
		},
		{
			path: '/monitoring/trendAnalysis',
			name: 'TrendAnalysis',
			component: TrendAnalysis
		},
		{
			path:'/monitoring/deviceList',
			name:'DeviceList',
			component:DeviceList
		},
		{
			path: '/analysis',
			name: 'Analysis',
			component: Analysis
		},
		{
			path: '/me/password',
			name: 'Password',
			component: Password
		},
		{
			path: '/me/about',
			name: 'About',
			component: About
		},
		{
			path: '/me/userSetting',
			name: 'UserSetting',
			component: UserSetting
		},
		{
			path: '/me/recommend',
			name: 'Recommend',
			component: Recommend
		},
		{
			path: '/me/choiseCompany',
			name: 'ChoiseCompany',
			component: ChoiseCompany
		},
		{
			path: '/me/choiseSecondCompany',
			name: 'ChoiseSecondCompany',
			component: ChoiseSecondCompany
		},
		{
			path: '/me/choiseWorkShop',
			name: 'ChoiseWorkShop',
			component: ChoiseWorkShop
		},
		{
			path: '/me/choiseWorkStation',
			name: 'ChoiseWorkStation',
			component: ChoiseWorkStation
		},
		{
			path: '/moudules/scanQRCode',
			name: 'ScanQRCode',
			component: ScanQRCode
		},
		{
			path: '/scan/WorkOrderInformation',
			name: 'WorkOrderInformation',
			component: WorkOrderInformation
		},
		{
			path: '/message/sysMessageInfo',
			name: 'SysMessageInfo',
			component: SysMessageInfo
		},
		{
			path: '/message/eventMessageInfo',
			name: 'EventMessageInfo',
			component: EventMessageInfo
		}
	]
})
